import { createStore } from "vuex";
import createPersistedState from "vuex-persistedstate";
const store = createStore({
  state: {
    m_name: null,
    m_id: null,
  },
  plugins: [createPersistedState()], //f5 ÎàåÎ†ÄÏùÑ Îïå localÏóêÏÑú Í∞ÄÏ†∏Ïò§Í∏∞
  getters: {
    getUserName(state) {
      return state.m_name;
    },
    getUserId(state) {
      return state.m_id;
    },
  },
  mutations: {
    //Î≥ÄÍ≤ΩÎê† Îïå ÎåÄÏã† ÎèôÏûëÌï† ÏòàÏ†ï
    setUser(state, userInfo) {
      state.m_name = userInfo.m_name;
      state.m_id = userInfo.m_id;
    },
    logout(state) {
      state.m_name = null;
      state.m_id = null;
    },
  },
  actions: {},
});
export default store;
