<template>
  <div class="login">
    <h1>LOGIN</h1>
    <input v-model="userId" type="text" />
    <input v-model="userPassword" type="password" />
    <button class="btn" @click="loginSubmit">LOGIN</button>
    {{ error }}
  </div>
</template>

<script>
export default {
  data() {
    return {
      userId: null,
      userPassword: null,
      error: '',
      userInfo: {
        m_name: this.$store.getters.getUserName,
        m_id: this.$store.getters.getUserId,
      },
    };
  },
  methods: {
    loginSubmit() {
      //alert(this.userID + ":" + this.userPassword);
      let login = {};
      (login.userId = this.userId), (login.userPassword = this.userPassword);
      try {
        this.$axios
          .post(this.$server + "/login", JSON.stringify(login), {
            headers: { "Content-Type": `application/json` },
          })
          .then((res) => {
            if (res.status === 200) {
              if (res.data.count == 1) {
                (this.userInfo.m_name = res.data.m_name),
                  (this.userInfo.m_id = login.userId),
                  this.$store.commit("setUser", this.userInfo);
                this.$router.push("/boardList");
              } else {
                this.error = "Ïò¨Î∞îÎ•∏ ÏïÑÏù¥ÎîîÏôÄ ÏïîÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.";
              }
            }
          });
      } catch (error) {
        console.error(error);
      }
    },
  },
};
</script>

<style scoped>
.login {
  margin: 0 auto;
  width: 300px;
  height: 300px;
  background-color: black;
  color: white;
  padding: 10px;
}
.login input,
button {
  border: 0px;
  width: 100%;
  height: 30px;
  padding: 5px;
  margin-bottom: 5px;
  box-sizing: border-box;
}
</style>
